































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Chart from '@/components/Chart.vue';
import Tabs from '@/components/Tabs.vue';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import typeList from '@/constance/typeList';
import Notes from '@/components/Money/Notes.vue';
@Component({
  components: {Notes, Tabs, Chart}
})
export default class StatisticsChart extends Vue {
  typeList = typeList;
  type = '-';
  beforeCreate() {
    this.$store.commit('fetchRecord');
  }
  month(date: string){
   return  dayjs(date).format('M')
  }
  t(type: string){
    if(type==='-'){
      return '支出'
    }else {
      return '收入'
    }
  }
  get total(){
   let sum=0
    console.log(this.groupList);
    for(let i=0;i<this.groupList.length;i++){
      sum+=this.groupList[i].total
    }
    return sum
  }
  get record() {
    return this.$store.state.record;
  }
  get recordList() {
    return (this.$store.state as RootState).recordList;
  }
  get groupList() {
    const {recordList} = this;
    if (recordList.filter(i => i.type === this.type).length === 0) {
      return [];
    }else if(recordList.filter(i=>dayjs(i.createAt).isSame(dayjs(this.record.createAt),'month')).length===0){
      return []
    }
    const newRecordList = clone(recordList).filter(i => i.type === this.type)
        .filter(i=>dayjs(i.createAt).isSame(dayjs(this.record.createAt),'month'))
        .sort((a, b) => dayjs(b.createAt).valueOf() - dayjs(a.createAt).valueOf());

    const result = [{title: dayjs(newRecordList[0].createAt).format('YYYY-MM-DD'), total:parseInt(newRecordList[0].amount.toString()), item: [newRecordList[0]]}];

    if (newRecordList.length === 0) {
      return [];
    }
    for (let i = 1; i < newRecordList.length; i++) {
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(newRecordList[i].createAt), 'day')) {
        last.item.push(newRecordList[i]);
      } else {

        result.push({title: dayjs(newRecordList[i].createAt).format('YYYY-MM-DD'), total: 0, item: [newRecordList[i]]});
      }
      result.map(group => {
        group.total = group.item.reduce((sum, item) => {

          return sum + parseInt(item.amount.toString());
        }, 0);
      });
    }
    return result;
  }

  get groupTag() {
    const {recordList} = this;
    if (recordList.filter(i => i.type === this.type).length === 0) {
      return [];
    }else if(recordList.filter(i=>dayjs(i.createAt).isSame(dayjs(this.record.createAt),'month')).length===0){
      return []
    }
    const newRecordList = clone(recordList).filter(i => i.type === this.type)
        .filter(i=>dayjs(i.createAt).isSame(dayjs(this.record.createAt),'month'))
        .sort((a, b) =>parseInt(a.tags[0].id) -parseInt(b.tags[0].id));
    const array = [{tagName:newRecordList[0].tags[0].name,item:[newRecordList[0]],total:0}]
    for (let i = 1; i < newRecordList.length; i++) {
      const last = array[array.length-1]
      if(last.tagName===newRecordList[i].tags[0].name){
        last.item.push(newRecordList[i])
      }else {
        array.push({tagName:newRecordList[i].tags[0].name,item: [newRecordList[i]],total:0})
      }
    }
    array.map(group => {
      group.total = group.item.reduce((sum, item) => {

        return sum + parseInt(item.amount.toString());
      }, 0);
    });

    return array
  }

  get chartPie(){
    const array= []
    for(let i=0; i<this.groupTag.length;i++){

      array.push({value:this.groupTag[i].total,name:this.groupTag[i].tagName})
    }
    return {
      tooltip: {
        trigger: 'item',
        formatter: ' 金额:￥{c} 占比:{d}%',
      },
      legend: {
        top: '5%',
        left: 'center'
      },
      series: [
        {
          name: '123',
          type: 'pie',
          radius: ['40%', '70%'],
          avoidLabelOverlap: false,
          itemStyle: {
            borderRadius: 10,
            borderColor: '#fff',
            borderWidth: 2
          },
          label: {
            show: false,
            position: 'center'
          },
          emphasis: {
            label: {
              show: true,
              fontSize: '40',
              fontWeight: 'bold'
            }
          },
          labelLine: {
            show: false
          },
          data:array
        }
      ]
    }
  }
}

