
















































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Type from '@/components/Money/Type.vue';
import Tabs from '@/components/Tabs.vue';
import intervalList from '@/constance/intervalList';
import typeList from '@/constance/typeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import Chart from '@/components/Chart.vue';
import _ from 'lodash';
import Notes from '@/components/Money/Notes.vue';


@Component({
  components: {Notes, Chart, Tabs, Type,}
})
export default class Statistics extends Vue {
  type = '-';
  typeList = typeList;

  beforeCreate() {
    this.$store.commit('fetchRecord');
  }

  // mounted() {
  //   const div = this.$refs.chartWrapper as HTMLDivElement;
  //   div.scrollLeft = div.scrollWidth;
  // }

  remove(id: string){
    this.$store.commit('removeRecord',id)
  }

  get record() {
    return this.$store.state.record;
  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  timeDay(date: string) {
    const day = dayjs(date);
    const now = dayjs();

    if (day.isSame(now, 'day')) {
      return '今天';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '昨天';
    } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
      return '前天';
    } else if (day.isSame(now, 'year')) {
      return day.format('M月D日');
    } else {
      return day.format('YYYY年M月D日');
    }
  }



  get chartLineKeyValue() {
    const today = new Date();
    const array = [];
    for (let i = 0; i <= 29; i++) {
      const dayString = dayjs(today).subtract(i, 'day').format('YYYY-MM-DD');
      const found = _.find(this.groupList, {title: dayString});
      array.push({key: dayString, value: found ? found.total : 0});
      array.sort((a, b) => {
        if (a.key > b.key) {
          return 1;
        } else if (a.key === b.key) {
          return 0;
        } else {
          return -1;
        }
      });
    }
    return array;
  }



  get chartLine() {
    const keys = this.chartLineKeyValue.map(i => i.key);
    const values = this.chartLineKeyValue.map(i => i.value);
    return {
      grid: {
        top: 10,
        left: 5,
        right: 5,
        bottom: 30
      },
      xAxis: {
        axisTick: {alignWithLabel: true},
        type: 'category',
        data: keys
      },
      yAxis: {
        type: 'value',
        show: false
      },
      series: [{
        symbolSize: 12,
        data: values,
        type: 'line'
      }],
      tooltip: {show: true}
    };
  }

  get chartPie(){
    const array= []
    for(let i=0; i<this.groupTag.length;i++){

      array.push({value:this.groupList[i].total,name:this.groupTag[i].tagName})
    }
    return {
      label: {
        formatter: '{b}\n金额:￥{c} \n 占比:{d}%',
        verticalAlign:'middle',
        lineHeight:20,
        align:'left'
      },
      tooltip: {
        trigger: 'item',
        formatter: ' 金额:￥{c} 占比:{d}%',
      },

      series: [
        {
          type: 'pie',
          radius: '50%',
          data: array,
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            },
            label : {
              show:true,
              position:'outside',

            },
          }
        }
      ]
    }
  }


  get groupTag() {
    const {recordList} = this;
    if (recordList.filter(i => i.type === this.type).length === 0) {
      return [];
    }
    const newRecordList = clone(recordList).filter(i => i.type === this.type)
        .sort((a, b) =>parseInt(a.tags[0].id) -parseInt(b.tags[0].id));
    const array = [{tagName:newRecordList[0].tags[0].name,item:[newRecordList[0]],total:0}]
    for (let i = 1; i < newRecordList.length; i++) {
      const last = array[array.length-1]
      if(last.tagName===newRecordList[i].tags[0].name){
          last.item.push(newRecordList[i])
      }else {
        array.push({tagName:newRecordList[i].tags[0].name,item: [newRecordList[i]],total:0})
      }
    }
    array.map(group => {
      group.total = group.item.reduce((sum, item) => {

        return sum + parseInt(item.amount.toString());
      }, 0);
    });

   return array
  }

  get groupList() {
    const {recordList} = this;
    if (recordList.filter(i => i.type === this.type).length === 0) {
      return [];
    }else if(recordList.filter(i=>dayjs(i.createAt).isSame(dayjs(this.record.createAt),'month')).length===0){
      return []
    }
    const newRecordList = clone(recordList).filter(i => i.type === this.type)
        .filter(i=>dayjs(i.createAt).isSame(dayjs(this.record.createAt),'month'))
        .sort((a, b) => dayjs(b.createAt).valueOf() - dayjs(a.createAt).valueOf());

    const result = [{title: dayjs(newRecordList[0].createAt).format('YYYY-MM-DD'), total: newRecordList[0].amount, item: [newRecordList[0]]}];

    if (newRecordList.length === 0) {
      return [];
    }
    for (let i = 1; i < newRecordList.length; i++) {
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(newRecordList[i].createAt), 'day')) {
        last.item.push(newRecordList[i]);
      } else {

        result.push({title: dayjs(newRecordList[i].createAt).format('YYYY-MM-DD'), total: 0, item: [newRecordList[i]]});
      }
      result.map(group => {
        group.total = group.item.reduce((sum, item) => {

          return sum + parseInt(item.amount.toString());
        }, 0);
      });
    }
    return result;
  }


}
